@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    font-family: "Open Sans", serif;
    color: aliceblue;
}



.title-container__subtitle {
    font-style: italic;
    font-weight: 100;
    letter-spacing: 1px;
    line-height: 1.5;
    font-family: "Merriweather", serif;
    color: aquamarine;
}

.wrapper {

    background: -webkit-linear-gradient(top, #1e5799 0%, #2989d8 65%, #207cca 51%, #7db9e8 100%);
}

.weather__city {
    font-size: 30px;
}

.weather__key {

    border-bottom: solid 2px rgba(255, 255, 255, 0.06);
    padding: 20px 0 20px 0;
    font-weight: 500;
}

.weather__key:last-child {
    border: 0;
}

.weather__value {

    font-weight: 200;
}

.weather__error {
    color: aquamarine;
    font-size: 20px;
    letter-spacing: 1px;
    font-weight: 200;
}
